/******************************/
/********** HTML TAGS *********/
/******************************/
html {
  font-size: 0.625em;
}

body {
  height: 100%;
  margin: 0;
  padding: 0;
  background: #948B82;
  overflow-x: hidden;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}

a {
  color: #fff;
  text-decoration: none;
  transition: all 0.3s;
}
a:hover, a:focus, a:active {
  text-decoration: none;
}

hr {
  border-top: 1px solid #ddd;
  margin-top: 1.2rem;
  margin-bottom: 1.2rem;
  width: 100%;
}
hr.white_hr {
  border-top: 1px solid #707070;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.estilos_botones, .input__label, button.btn, input, input[type=text], input[type=email], input[type=tel],
select, textarea, .btn, .vc_btn3 {
  text-transform: uppercase;
  padding: 1rem 2rem;
  font-size: 1.3rem;
  line-height: 1.2em;
  border: 0;
}
@media (min-width: 992px) {
  .estilos_botones, .input__label, button.btn, input, input[type=text], input[type=email], input[type=tel],
  select, textarea, .btn, .vc_btn3 {
    padding: 1.5rem 4rem;
    font-size: 1.2rem;
  }
}
@media (min-width: 1200px) {
  .estilos_botones, .input__label, button.btn, input, input[type=text], input[type=email], input[type=tel],
  select, textarea, .btn, .vc_btn3 {
    font-size: 1.3rem;
  }
}
.estilos_botones span, .input__label span, input span, input[type=text] span, input[type=email] span, input[type=tel] span,
select span, textarea span, .btn span, .vc_btn3 span {
  display: inline-block;
}

.btn, .vc_btn3 {
  color: #fff !important;
  background: #544646 !important;
  border-radius: 100px;
  border: 2px solid #fff;
  transition: all 0.3s;
}
.btn:hover, .vc_btn3:hover {
  background: #fff !important;
  color: #544646 !important;
}
.btn path, .vc_btn3 path {
  fill: #fff !important;
}

.btn_white {
  background: transparent !important;
  border: 2px solid #fff;
}
.btn_white:hover {
  background: transparent !important;
  color: #544646 !important;
  border: 2px solid #544646;
}

.btn__arrow {
  position: relative;
  padding-right: 7.2rem !important;
}
.btn__arrow .button_modal_gallery__icon {
  width: 19px;
  height: 10px;
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translate(-50%);
  overflow: hidden;
}
.btn__arrow .button_modal_gallery__icon:before {
  content: "";
  display: block;
  height: 10px;
  width: 19px;
  background: url(../img/arrow_btn.svg);
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(-50%);
}
.btn__arrow svg {
  position: absolute;
  right: 2.5rem;
  top: 50%;
  width: 19px;
  transform: translate(-50%);
  transition: all 0.3s;
}
.btn__arrow:hover {
  padding-right: 10rem !important;
}
.btn__arrow:hover .button_modal_gallery__icon:before {
  animation-name: view_button;
  animation-duration: 0.8s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
}
.btn__arrow:hover svg {
  width: 0 !important;
  height: 10px !important;
}

@keyframes view_button {
  0% {
    left: 0;
  }
  50% {
    left: 150%;
  }
  51% {
    left: unset;
    right: 100%;
  }
  80% {
    right: 25%;
  }
  100% {
    right: 0;
  }
}
.owl-item {
  overflow: hidden;
}

.owl-nav {
  position: unset;
}
.owl-nav button {
  position: absolute;
  top: 50%;
  transform: translate(-50%);
  overflow: hidden;
  text-indent: -999px;
  width: 13px;
  height: 34px;
}
@media (min-width: 992px) {
  .owl-nav button {
    width: 25px;
    height: 64px;
  }
}
