.modal__fullscreen {
  padding: 0 !important;
  z-index: 999999;
}

.modal__fullscreen .modal-dialog {
  max-width: none !important;
  top: 0;
  width: 100% !important;
  height: 100vh !important;
  margin: 0 !important;
  overflow: hidden;
  -webkit-transform: none !important;
  transform: none !important;
}
.modal__fullscreen .modal-dialog .modal-content {
  margin: 0 !important;
  padding: 0 !important;
  background: #39312C;
}
.modal__fullscreen .modal-dialog .modal-body {
  padding: 0 !important;
  min-height: 100vh !important;
}
.modal__fullscreen .modal-dialog .modal_navigation {
  position: fixed;
  z-index: 3000;
  width: 100%;
  bottom: 0;
  left: 0;
  padding: 0 3rem 3rem 3rem;
  display: flex;
  background: #000;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.5) 0, rgba(0, 0, 0, 0) 100%);
  font-size: 1.2rem;
}
.modal__fullscreen .modal-dialog .modal_navigation .image-caption {
  position: relative;
  width: 100%;
  padding-top: 3rem;
}
.modal__fullscreen .modal-dialog .modal_navigation .image-caption:before {
  display: block;
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background: #fff;
  top: 0;
  left: 0;
}
.modal__fullscreen .modal-dialog .owl-carousel .owl-stage-outer {
  min-width: 100%;
}
.modal__fullscreen .modal-dialog .owl-item, .modal__fullscreen .modal-dialog .item {
  width: 100%;
  height: 100vh !important;
}
.modal__fullscreen .modal-dialog .owl-item img, .modal__fullscreen .modal-dialog .item img {
  height: 100%;
  width: 100%;
  -o-object-position: center bottom;
  object-position: center bottom;
  -o-object-fit: cover;
  object-fit: cover;
}

button.close {
  letter-spacing: 2px;
  position: absolute;
  z-index: 999999;
  color: #fff;
  font-size: 12px;
  text-shadow: none;
  font-weight: 300;
  opacity: 1 !important;
  text-transform: uppercase;
  transition: all 0.3s;
}
button.close:hover {
  opacity: 0.8;
  color: #fff;
}
@media (min-width: 768px) {
  button.close img {
    margin-right: 20px;
  }
}
@media (max-width: 767px) {
  button.close {
    bottom: 3rem;
    left: 50%;
    transform: translate(-50%);
    background: #544646;
    border-radius: 50px;
    border: 2px solid #fff;
    padding: 10px;
  }
}
@media (min-width: 768px) {
  button.close {
    top: 6rem;
    left: 50%;
    transform: translate(-50%);
    font-size: 14px;
  }
}
@media (min-width: 1200px) {
  button.close {
    top: 8rem;
  }
}